// components/HowItWorksSection.tsx
"use client";

import { MapPin, Calendar, Bus, ArrowRight, CheckCircle } from "lucide-react";

export default function HowItWorks() {
  const steps = [
    {
      Icon: MapPin,
      title: "Choose your route",
      desc: "Select your pickup and drop-off points anywhere in Belgium to plan your journey.",
      color: "text-blue-600",
      bgColor: "bg-blue-100"
    },
    {
      Icon: Calendar,
      title: "Pick your date and time",
      desc: "Choose when you want to travel — one-way or return, we'll be ready when you are.",
      color: "text-green-600",
      bgColor: "bg-green-100"
    },
    {
      Icon: Bus,
      title: "Confirm your booking",
      desc: "Complete your reservation and our team will assign a modern, comfortable coach with a professional driver.",
      color: "text-purple-600",
      bgColor: "bg-purple-100"
    },
  ];

  return (
    <section className="py-20 bg-white relative overflow-hidden">
      {/* Background Elements */}
      <div className="absolute top-0 left-0 w-full h-1 bg-gradient-to-r from-blue-500 to-indigo-500"></div>
      <div className="absolute top-10 right-10 w-64 h-64 bg-blue-100 rounded-full opacity-20 blur-3xl"></div>
      <div className="absolute bottom-10 left-10 w-64 h-64 bg-indigo-100 rounded-full opacity-20 blur-3xl"></div>
      
      <div className="max-w-7xl mx-auto px-6 lg:px-8 text-center relative z-10">
        {/* Header */}
        <div className="mb-16 animate-slide-up">
          <span className="inline-flex items-center bg-blue-100 text-blue-600 px-4 py-2 rounded-full text-sm font-semibold mb-4 shadow-sm">
            <CheckCircle className="h-4 w-4 mr-2" />
            How it works
          </span>
          <h2 className="text-4xl lg:text-5xl font-bold text-gray-900 mb-4">
            Rent with our simple 3-step process
          </h2>
          <p className="text-gray-600 text-xl max-w-2xl mx-auto">
            Getting your group transportation sorted has never been easier. Follow these simple steps to book your journey.
          </p>
        </div>

        {/* Steps */}
        <div className="relative">
          {/* Connecting line for desktop */}
          <div className="hidden lg:block absolute top-20 left-1/4 right-1/4 h-2 bg-gradient-to-r from-blue-200 via-green-200 to-purple-200 -z-10 rounded-full"></div>
          
          <div className="grid grid-cols-1 md:grid-cols-3 gap-12">
            {steps.map(({ Icon, title, desc, color, bgColor }, index) => (
              <div key={title} className="relative animate-slide-up" style={{animationDelay: `${index * 0.2}s`}}>
                <div className="flex flex-col items-center">
                  {/* Step Number */}
                  <div className="w-16 h-16 bg-gradient-to-br from-blue-600 to-indigo-600 text-white rounded-full flex items-center justify-center text-xl font-bold mb-6 shadow-lg transform hover:scale-110 transition-transform duration-300 relative z-10">
                    {index + 1}
                  </div>
                  
                  {/* Icon */}
                  <div className={`w-20 h-20 ${bgColor} rounded-2xl flex items-center justify-center mb-6 shadow-md transform hover:scale-110 transition-transform duration-300`}>
                    <Icon className={`h-10 w-10 ${color}`} />
                  </div>
                  
                  {/* Content */}
                  <h3 className="text-2xl font-semibold text-gray-900 mb-4">{title}</h3>
                  <p className="text-gray-600 text-lg leading-relaxed">{desc}</p>
                </div>
                
                {/* Arrow for mobile */}
                {index < steps.length - 1 && (
                  <div className="md:hidden flex justify-center mt-8">
                    <ArrowRight className="h-8 w-8 text-blue-400 transform rotate-90 animate-bounce" />
                  </div>
                )}

                {/* Arrow for desktop */}
                {index < steps.length - 1 && (
                  <div className="hidden lg:block absolute top-20 right-0 transform translate-x-1/2">
                    <ArrowRight className="h-8 w-8 text-blue-400 animate-pulse" />
                  </div>
                )}
              </div>
            ))}
          </div>
        </div>

        {/* CTA */}
        <div className="mt-16 animate-fade-in" style={{animationDelay: '0.6s'}}>
          <div className="inline-flex items-center space-x-2 bg-blue-50 rounded-2xl px-6 py-4 border border-blue-100 shadow-sm hover:shadow-md transition-shadow">
            <Bus className="h-6 w-6 text-blue-600" />
            <span className="text-gray-700 font-semibold">
              Ready to book? Get your quote in minutes!
            </span>
          </div>
        </div>
      </div>
    </section>
  );
}