// components/WhyChooseUsSection.tsx
"use client";

import Image from "next/image";
import {
  ShieldCheck,
  User,
  Clock,
  Headset,
  CheckCircle,
  DollarSign,
  ThumbsUp
} from "lucide-react";

export default function WhyChooseUs() {
  const features = [
    {
      Icon: DollarSign,
      title: "Best Price Guarantee",
      desc: "With Belgium Buses, you always get the best value across Belgium. If you find a lower quote for the same journey, we'll match it so you travel for less without compromising comfort.",
      color: "text-green-600",
      bgColor: "bg-green-100"
    },
    {
      Icon: User,
      title: "Experienced Drivers",
      desc: "Our professional and friendly drivers are fully licensed and trained to make every trip safe, smooth, and enjoyable from start to finish.",
      color: "text-blue-600",
      bgColor: "bg-blue-100"
    },
    {
      Icon: Clock,
      title: "Always Available",
      desc: "From early morning airport transfers to late-night returns, our fleet operates 24 hours a day to fit your travel plans anywhere in Belgium.",
      color: "text-purple-600",
      bgColor: "bg-purple-100"
    },
    {
      Icon: Headset,
      title: "24/7 Support",
      desc: "Our dedicated customer service team is available around the clock to help with bookings, updates, or travel assistance whenever you need it.",
      color: "text-orange-600",
      bgColor: "bg-orange-100"
    },
  ];

  return (
    <section className="py-20 bg-gradient-to-br from-gray-50 to-blue-50 relative overflow-hidden">
      {/* Background Elements */}
      <div className="absolute top-0 left-0 w-full h-1 bg-gradient-to-r from-blue-500 to-indigo-500"></div>
      <div className="absolute top-10 right-10 w-64 h-64 bg-blue-200 rounded-full opacity-20 blur-3xl"></div>
      <div className="absolute bottom-10 left-10 w-64 h-64 bg-indigo-200 rounded-full opacity-20 blur-3xl"></div>
      
      <div className="max-w-7xl mx-auto px-6 lg:px-8 grid grid-cols-1 lg:grid-cols-2 gap-16 items-center relative z-10">
        {/* Content Section */}
        <div className="space-y-8 animate-slide-up">
          <div className="space-y-4">
            <span className="inline-flex items-center bg-blue-100 text-blue-600 px-4 py-2 rounded-full text-sm font-semibold shadow-sm">
              <ThumbsUp className="h-4 w-4 mr-2" />
              Why Choose Us
            </span>
            <h2 className="text-4xl lg:text-5xl font-bold text-gray-900 leading-tight">
              Why Choose Belgium Buses
            </h2>
            <p className="text-gray-700 text-lg leading-relaxed">
              Choose <strong className="text-blue-600">Belgium Buses</strong> for reliable, comfortable, and affordable group transport. We make every journey across Belgium effortless with professional drivers and modern vehicles.
            </p>
          </div>
          
          <div className="space-y-6">
            {features.map(({ Icon, title, desc, color, bgColor }, index) => (
              <div 
                key={title} 
                className="flex items-start gap-6 p-6 bg-white/80 backdrop-blur-sm rounded-2xl shadow-sm border border-gray-100 hover:shadow-lg hover:border-blue-200 transition-all duration-300 transform hover:-translate-y-1 animate-slide-up"
                style={{animationDelay: `${0.2 + index * 0.1}s`}}
              >
                <div className={`flex-shrink-0 ${bgColor} p-4 rounded-xl shadow-sm`}>
                  <Icon className={`h-7 w-7 ${color}`} />
                </div>
                <div className="flex-1">
                  <h3 className="text-xl font-semibold text-gray-900 mb-2">
                    {title}
                  </h3>
                  <p className="text-gray-600 leading-relaxed">{desc}</p>
                </div>
              </div>
            ))}
          </div>

          {/* Additional Trust Indicators */}
          <div className="flex flex-wrap gap-6 pt-4 animate-fade-in" style={{animationDelay: '0.6s'}}>
            <div className="flex items-center space-x-2 text-gray-600 bg-white px-4 py-2 rounded-lg shadow-sm">
              <div className="w-3 h-3 bg-green-500 rounded-full animate-pulse"></div>
              <span className="text-sm font-medium">250,000+ Passengers</span>
            </div>
            <div className="flex items-center space-x-2 text-gray-600 bg-white px-4 py-2 rounded-lg shadow-sm">
              <div className="w-3 h-3 bg-blue-500 rounded-full"></div>
              <span className="text-sm font-medium">50+ Cities Served</span>
            </div>
            <div className="flex items-center space-x-2 text-gray-600 bg-white px-4 py-2 rounded-lg shadow-sm">
              <div className="w-3 h-3 bg-purple-500 rounded-full"></div>
              <span className="text-sm font-medium">24/7 Operation</span>
            </div>
          </div>
        </div>

        {/* Image Section */}
        <div className="flex justify-center lg:justify-end relative animate-float">
          <div className="relative">
            <div className="relative z-10 rounded-2xl overflow-hidden shadow-2xl transform hover:scale-[1.02] transition-transform duration-500 group">
              <Image
                src="/images/bus2.png"
                alt="Modern coach from Belgium Buses in Belgium"
                width={600}
                height={400}
                className="object-cover w-full h-auto"
              />
              <div className="absolute inset-0 bg-gradient-to-t from-black/20 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
            </div>
            
            {/* Floating Badge */}
            <div className="absolute -bottom-6 -left-6 bg-white rounded-2xl shadow-xl p-6 border border-gray-100 animate-pulse-slow">
              <div className="flex items-center space-x-3">
                <div className="w-12 h-12 bg-green-100 rounded-xl flex items-center justify-center">
                  <CheckCircle className="h-6 w-6 text-green-600" />
                </div>
                <div>
                  <p className="text-lg font-bold text-gray-900">50+</p>
                  <p className="text-sm text-gray-600">Modern Vehicles</p>
                </div>
              </div>
            </div>

            {/* Second Floating Badge */}
            <div className="absolute -top-6 -right-6 bg-white rounded-2xl shadow-xl p-6 border border-gray-100 animate-pulse-slow" style={{animationDelay: '1s'}}>
              <div className="flex items-center space-x-3">
                <div className="w-12 h-12 bg-blue-100 rounded-xl flex items-center justify-center">
                  <ShieldCheck className="h-6 w-6 text-blue-600" />
                </div>
                <div>
                  <p className="text-lg font-bold text-gray-900">100%</p>
                  <p className="text-sm text-gray-600">Insured</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}